import React, { useState } from 'react';
// @ts-ignore
import { ReactHashCalendar } from 'react-hash-calendar'
import dayjs from "dayjs";
import { List } from "antd-mobile";
import Ying from "../../../Ying";

const Calender: React.FC = () => {

    const [date, setDate] = useState<Date>();

    return (
        <>
            <ReactHashCalendar
                model="inline"
                markDate={Ying.calender}
                dateClickCallback={(date: Date | string) => {
                    if (date instanceof Date) {
                        setDate(date);
                    }
                }}
            />
            <List mode='card'>
                {Ying.calender.filter(item => {
                    for (let date0 of item.date) {
                        if(dayjs(date0, 'YYYY/MM/DD').isSame(dayjs(date), 'day')) {
                            return true;
                        }
                    }
                    return false;
                }).map(item =>
                    <List.Item title={item.thing.title}>
                        {item.thing.content}
                    </List.Item>
                )}
            </List>
        </>
    );
};

export default Calender;
