import React from "react";
import { Card, Grid } from "antd-mobile";

const CheckCard: React.FC<{
    icon: any,
    name: any,
    description: any,
    onClick: (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => void
}> = (props) => {
    return (
        <Card onClick={props.onClick}>
            <Grid columns={24}>
                <Grid.Item span={5}>
                    {props.icon}
                </Grid.Item>
                <Grid.Item span={19}>
                    <div
                        style={{
                            position: 'absolute',
                            marginTop: -2.604
                        }}
                    >
                        <span>
                            <strong>
                                {props.name}
                            </strong>
                        </span>
                        <br/>
                        <span
                            style={{
                                color: '#969696',
                                fontSize: 12
                            }}
                        >
                            {props.description}
                        </span>
                    </div>
                </Grid.Item>
            </Grid>
        </Card>
    );
}

export default CheckCard;
