import React, { useEffect, useState } from 'react';
import { useLocation, useNavigate, useRoutes } from 'react-router-dom';
import { ErrorBlock, FloatingBubble } from "antd-mobile";
import Index from "./pages/apps/courseTable";
import Workspace from "./pages/workspace";
import { Home } from "@icon-park/react";
import Calender from "./pages/apps/calender";
import Plan from "./pages/apps/plan";
import styles from './App.less';
import Thing from "./pages/apps/thing";

function App() {

    const [display, setDisplay] = useState(false);
    const location = useLocation();
    const navigate = useNavigate();
    useEffect(() => {
        setDisplay(location.pathname !== '/');
    }, [location.pathname]);

    const routers = [
        {path: '/', element: <Workspace/>},
        {path: '/apps/courseTable', element: <Index/>},
        {path: '/apps/calender', element: <Calender/>},
        {path: '/apps/thing', element: <Thing />},
        {path: '/apps/plan', element: <Plan/>},
        {path: '*', element: <ErrorBlock status='empty' fullPage/>}
    ];

    return (
        <div className={styles.app}>
            <div className={styles.body}>
                {useRoutes(routers)}
            </div>
            <>
                {
                    display ? (
                        <FloatingBubble
                            style={{
                                '--initial-position-bottom': '24px',
                                '--initial-position-right': '24px',
                                // '--edge-distance': '24px',
                            }}
                            onClick={() => navigate('/')}
                        >
                            <Home theme="outline" size="24" fill="#fff"/>
                        </FloatingBubble>
                    ) : null
                }
            </>
        </div>

    );

}

export default App;
