import { List } from 'antd-mobile';
import React from 'react';
import { Optimize, Schedule } from "@icon-park/react";
import Ying from "../../../Ying";
import { format, sections } from "../courseTable/components/Frame";
import dayjs from "dayjs";
import { getExtra } from "../thing";

const Plan: React.FC = () => {

    const now = dayjs();
    const rawPlan: {
        avatar: JSX.Element;
        name: string;
        description: string | JSX.Element;
        extra: string;
        timestamp: number;
    } [] = [];
    Ying.courses.filter(course => {
        for (let schedule of course.schedules) {
            if (schedule.day === Ying.current.day && schedule.weeks.indexOf(Ying.current.week) !== -1) {
                return true;
            }
        }
        return false;
    }).forEach(course => {
        const schedule = course.schedules.filter(schedule => schedule.day === Ying.current.day)[0];
        rawPlan.push({
            avatar: <Schedule theme="outline" size="24" fill={course.color}/>,
            name: `${course.name}@${course.room}`,
            description: `课程表: 第${schedule.start}-${schedule.end}`,
            extra: `${sections[schedule.start][0].format(format)}-${sections[schedule.end][1].format(format)}`,
            timestamp: sections[schedule.start][0].unix()
        });
    });
    Ying.things.filter(thing => {
        if (thing.schedule?.day) {
            if (thing.schedule.day.length === 1) {
                return thing.schedule.day[0].isSame(now, 'day');
            } else if (thing.schedule.day.length === 2) {
                return (
                    thing.schedule.day[0].isBefore(now, 'day')
                    &&
                    thing.schedule.day[1].isAfter(now, 'day')
                );
            }
            return false;
        } else {
            return true;
        }
    }).forEach(thing => {
        rawPlan.push({
            avatar: <Optimize theme="outline" size="24" fill="#333"/>,
            name: thing.title,
            description: thing.content,
            extra: getExtra(thing, false),
            timestamp: thing.schedule?.time[0].unix() || -Number.MAX_VALUE
        });
    });

    return (
        <>
            <>
                <strong
                    style={{
                        fontSize: 22
                    }}
                >
                    {now.format("MM/DD")}
                </strong>
            </>
            <List>
                {rawPlan.sort((x, y) =>
                    x.timestamp - y.timestamp
                ).map(plan => (
                    <List.Item
                        key={plan.name}
                        prefix={plan.avatar}
                        extra={plan.extra}
                        description={plan.description}
                    >
                        {plan.name}
                    </List.Item>
                ))}
            </List>
        </>
    );
};

export default Plan;
