import React, { CSSProperties, useState } from 'react';
import dayjs from "dayjs";
import { AutoCenter, Card, Grid, NavBar } from "antd-mobile";
import Ying from "../../../../Ying";
import { blue } from "@ant-design/colors";

export const format = "HH:mm";
export const sections = [
    [dayjs('0000-00-00 08:00'), dayjs('0000-00-00 08:50')],
    [dayjs('0000-00-00 09:00'), dayjs('0000-00-00 09:50')],
    [dayjs('0000-00-00 10:10'), dayjs('0000-00-00 11:00')],
    [dayjs('0000-00-00 11:10'), dayjs('0000-00-00 12:00')],
    [dayjs('0000-00-00 14:30'), dayjs('0000-00-00 15:20')],
    [dayjs('0000-00-00 15:30'), dayjs('0000-00-00 16:20')],
    [dayjs('0000-00-00 16:30'), dayjs('0000-00-00 17:20')],
    [dayjs('0000-00-00 17:30'), dayjs('0000-00-00 18:20')],
    [dayjs('0000-00-00 19:00'), dayjs('0000-00-00 19:50')],
    [dayjs('0000-00-00 20:00'), dayjs('0000-00-00 20:50')],
];
const days = ['一', '二', '三', '四', '五', '六', '七'];

const Frame: React.FC<{
    current: {
        week: number,
        month: number;
    }
}> = (props) => {

    const [width, setWidth] = useState<number>(0);
    const [height, setHeight] = useState<number>(0);
    const courses = Ying.courses.filter(course => {
        for (let schedule of course.schedules) {
            if (schedule.weeks.indexOf(props.current.week) !== -1) {
                return true;
            }
        }
        return false;
    });

    return (
        <>
            <NavBar backArrow={false}>
                第 {props.current.week} 周
            </NavBar>
            <Grid columns={24} gap={6}>
                <Grid.Item span={2}>
                    <Grid.Item>
                        <Card>
                            <AutoCenter>
                                {props.current.month} 月
                            </AutoCenter>
                        </Card>
                    </Grid.Item>
                    <br/>
                    <Grid columns={1} gap={6}>
                        {sections.map((item, index) =>
                            <Grid.Item>
                                <div
                                    ref={element => {
                                        if (element) {
                                            setHeight(element.getBoundingClientRect().height);
                                        }
                                    }}
                                >
                                    <Card>
                                        <AutoCenter>
                                            {index}
                                        </AutoCenter>
                                        <AutoCenter>
                                            {item[0].format(format)}
                                        </AutoCenter>
                                        <AutoCenter>
                                            {item[1].format(format)}
                                        </AutoCenter>
                                    </Card>
                                </div>
                            </Grid.Item>
                        )}
                    </Grid>
                </Grid.Item>
                <Grid.Item span={22}>
                    <Grid columns={days.length} gap={6}>
                        {days.map((item, index) =>
                            <Grid.Item>
                                <div
                                    ref={element => {
                                        if (element) {
                                            setWidth(element.getBoundingClientRect().width);
                                        }
                                    }}
                                >
                                    <Card
                                        style={{
                                            background: (
                                                Ying.current.week === props.current.week
                                                &&
                                                Ying.current.day === index
                                            ) ? blue[4] : undefined,
                                        }}
                                    >
                                        <AutoCenter>
                                            {item}
                                        </AutoCenter>
                                    </Card>
                                </div>
                            </Grid.Item>
                        )}
                    </Grid>
                    <br/>
                    <Grid columns={days.length} gap={6}>
                        {days.map((_, day) =>
                            <Grid.Item>
                                <Grid columns={1} gap={6}>
                                    {courses.map(item => {
                                        let schedule = undefined;
                                        item.schedules.forEach(value => {
                                            if (
                                                value.day === day
                                                &&
                                                value.weeks.indexOf(props.current.week) !== -1
                                            ) {
                                                schedule = value;
                                            }
                                        });
                                        if (!schedule) return null;

                                        const fix = 6.04;
                                        const style: CSSProperties = {
                                            width: width,
                                            // @ts-ignore
                                            height: height * (schedule.end - schedule.start) * 2 + fix,
                                            color: 'white',
                                            background: item.color,
                                            wordWrap: 'break-word',
                                            padding: 0,
                                            position: 'absolute',
                                            // @ts-ignore
                                            top: (height + fix) * (schedule.start + 1.4),
                                        };

                                        return (
                                            <Grid.Item>
                                                <Card style={style}>
                                                    <div
                                                        style={{
                                                            transform: 'scale(0.864)',
                                                        }}
                                                    >
                                                        {item.name}@{item.room}
                                                        <AutoCenter>
                                                            {item.teacher}
                                                        </AutoCenter>
                                                    </div>
                                                </Card>
                                            </Grid.Item>
                                        );
                                    })}
                                </Grid>
                            </Grid.Item>
                        )}
                    </Grid>
                </Grid.Item>
            </Grid>
        </>
    );
};

export default Frame;
