import React, { useEffect, useState } from 'react';
import { Grid, Tag } from "antd-mobile";
import CheckCard from "./components/CheckCard";
import { CalendarDot, Optimize, Planet, Schedule } from "@icon-park/react";
import { useNavigate } from "react-router-dom";
import Ying from "../../Ying";
import dayjs from "dayjs";
import { magenta } from "@ant-design/colors";

const Workspace: React.FC = () => {

    const applications = [{
        icon: <Schedule theme="outline" size="24" fill="#333"/>,
        name: '课程表',
        description: `本学期有${Ying.courses.length}门课程`,
        onClick: () => navigate('/apps/courseTable')
    }, {
        icon: <Optimize theme="outline" size="24" fill="#333"/>,
        name: '事项',
        description: `共记录了${Ying.things.length}条事项`,
        onClick: () => navigate('/apps/thing')
    }, {
        icon: <CalendarDot theme="outline" size="24" fill="#333"/>,
        name: '日历',
        description: '(鬼知道这要写啥',
        onClick: () => navigate('/apps/calender')
    }];
    const navigate = useNavigate();
    const [version, setVersion] = useState(undefined);

    useEffect(() => {
        fetch('https://api.github.com/repos/iYarnFog/_plan').then(response => {
            response.json().then(result => {
                setVersion(
                    dayjs(result.pushed_at).format('YYYY.MM.DD-HHmmss')
                )
            });
        })
    }, []);

    return (
        <>
            <Grid
                columns={2}
                gap={6}
            >
                {applications.map(item =>
                    <Grid.Item>
                        <CheckCard
                            {...item}
                        />
                    </Grid.Item>
                )}
            </Grid>
            <br/>
            <Grid
                columns={1}
                style={{
                    padding: '0 25%'
                }}
            >
                <Grid.Item>
                    <CheckCard
                        icon={<Planet theme="outline" size="24" fill="#333"/>}
                        name="规划"
                        description="未来可期"
                        onClick={() => {
                            navigate('/apps/plan');
                        }}
                    />
                </Grid.Item>
            </Grid>
            <br/>
            <Grid
                columns={2}
                gap={6}
                style={{
                    padding: '0 32.5%',
                    position: 'absolute',
                    bottom: 0,
                }}
            >
                <Grid.Item>
                    Designed by&nbsp;
                    <a href="https://i.iying.top" target="_blank" rel="noreferrer">
                        <Tag color='primary' fill='outline' round>
                            浅念
                        </Tag>
                    </a>
                </Grid.Item>
                <Grid.Item>
                    Build&nbsp;
                    <a href="https://github.com/iYarnFog/_plan" target="_blank" rel="noreferrer">
                        <Tag color={magenta.primary} fill='outline' round>
                            {version}
                        </Tag>
                    </a>
                </Grid.Item>
            </Grid>
            <div
                style={{
                    width: '90%',
                    position: 'absolute',
                    bottom: 0,
                    justifyContent: 'center'
                }}
            >

            </div>
        </>
    );

};

export default Workspace;
