import React, { useState } from 'react';
import { Swiper } from "antd-mobile";
import Frame from "./components/Frame";
import Ying from "../../../Ying";

const CourseTable: React.FC = () => {

    let lastIndex = 0;
    const current = Ying.current;
    const [offset, setOffset] = useState(0);

    return (
        <>
            <Swiper
                indicator={() => null}
                onIndexChange={index => {
                    if (lastIndex === 0 && index === 2) {
                        // 向左越界
                        setOffset(offset - 3);
                        // 都为0的情况时在第三页向右滑动
                    } else if ((lastIndex === 2 && index === 0) || (lastIndex === 0 && index === 0)) {
                        // 向右越界
                        setOffset(offset + 3);
                    }
                    lastIndex = index;
                }}
            >
                <Swiper.Item key={current.week}>
                    <Frame
                        current={{
                            week: current.week + offset,
                            month: current.month
                        }}
                    />
                </Swiper.Item>
                <Swiper.Item key={current.week + 1}>
                    <Frame
                        current={{
                            week: current.week + offset + 1,
                            month: current.month
                        }}
                    />
                </Swiper.Item>
                <Swiper.Item key={current.week + 2}>
                    <Frame
                        current={{
                            week: current.week + offset + 2,
                            month: current.month
                        }}
                    />
                </Swiper.Item>
            </Swiper>
        </>
    );
};

export default CourseTable;
